import React from 'react'
import Grid from '@material-ui/core/Grid';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import {useState , useEffect} from 'react'
import FiberManualRecord from '@material-ui/icons/FiberManualRecord'
import ShowChart from '@material-ui/icons/ShowChart'
import Stop from '@material-ui/icons/Stop'
import {connect,dispatch} from 'react-redux'
import Button from '@material-ui/core/Button'
import UndoIcon from '@material-ui/icons/Undo';
import LayersClearIcon from '@material-ui/icons/LayersClear';
import TextField from '@material-ui/core/TextField';
import Slider from '@material-ui/core/Slider'
import Typography from '@material-ui/core/Typography'

const DrawToolBar = (props) => {


    const handleChange = (event ,newMode) => {
        props.ChangeMode(newMode)
    }

    const inputProps = {
        step : 10,
        min : 10,
        max : 100
    }

    const valuetext = (value) => {
     
        return `${value}CM`;
      }

    return (
        <Grid container direction = 'row' style = {{height : '100%' , width : '100%'}}>
           <Grid item xl = {1} md = {0} style = {{marginRight : '1vw'}}></Grid>
            <Grid item xs = {1} style = {{alignItems : 'center' , marginTop : '1vh'}}>
                <Button onClick = {props.Reset} style = {{ boxShadow : '2px 2px 2px 2px grey' , border : '2px solid grey'}}>
                    <LayersClearIcon></LayersClearIcon>
                </Button>
            </Grid>
            <Grid item xs = {1} style = {{alignItems : 'right',marginTop : '1vh'}}>                                        
                  <p style = {{marginRight:'-2vw'}}>Reset</p>                    
            </Grid>
            <Grid item xs = {1} style = {{alignItems : 'center' , marginTop : '1vh'}}>                                        
                    <Button onClick = {props.Undo} style = {{margin : '0px',boxShadow : '2px 2px 2px 2px grey' , border : '2px solid grey'}}>
                        <UndoIcon></UndoIcon>             
                    </Button>                                      
            </Grid>
            <Grid item xs = {1} style = {{alignItems : 'right',marginTop : '1vh'}}>                                        
                  <p style = {{marginRight:'-2vw'}}>Undo</p>                    
            </Grid>
            <Grid item xl = {2} xs = {3} style = {{alignItems : 'center' , width : '50%'}}>
                <ToggleButtonGroup size = "large" value = {props.mode} exclusive onChange = {handleChange} style = {{marginTop : '1vh' , boxShadow : '2px 2px 2px 2px grey'}}>
                    <ToggleButton value = "Rect">
                        <Stop/>
                    </ToggleButton>
                    <ToggleButton value = "Polygon">
                        <ShowChart/>
                    </ToggleButton>
                    <ToggleButton value = "Circle">
                        <FiberManualRecord/>
                    </ToggleButton>
                    <ToggleButton value = "Ellipse">
                        <FiberManualRecord/>
                    </ToggleButton>

                </ToggleButtonGroup>
            </Grid>
            <Grid item xs = {2}>
                <TextField style = {{marginTop : '1vh',width :'12vw' , direction : 'rtl'}}
                    id="standard-number"
                    label="proportion (meter per square)"
                    type="number"
                    defaultValue = {1}
                    value = {props.proportion}
                    onChange = {(event) => {props.ChangeProportion(event.target.value)}}
                    InputLabelProps={{
                        shrink: true,
                    }}
                    variant="outlined"
                />
            </Grid>
            
            <Grid item xs = {2}>
                <Typography style= {{marginTop:'1vh' , marginBottom : '-10px'}} id="discrete-slider" gutterBottom>
                    רמת דיוק (מרחק כל קפיצה בסנטימטר)
                </Typography>
                <Slider
                    defaultValue={10}
                    getAriaValueText={(value) => {valuetext(value)}}    
                    onChange = {(event , value) => {props.ChangeAccuracy(value)}}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"      
                    step={10}
                    marks
                    min={10}
                    max={100}
                    
                />              
            </Grid>
        </Grid>
    )

}


const mapStateToProps = (state) => {
    return {
        mode : state.konva.mode,
        proportion : state.konva.proportion,
        accuracy : state.konva.accuracy
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        ChangeMode: (newMode) => dispatch({type : 'ChangeMode' , newMode : newMode}),
        Undo : () => dispatch({type: 'Undo'}),
        Reset : () => dispatch({type: 'Reset'}),
        ChangeProportion : (newProportion) => dispatch({type : 'ChangeProportion' , newProportion : newProportion }),
        ChangeAccuracy : (newAccuracy) => dispatch({type : 'ChangeAccuracy' , newAccuracy : newAccuracy })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DrawToolBar)